/**
 * @category 
 * @module 
 */

import { keyBy } from "lodash-es";
/**
 * 
 * @param {Array} arrs - 
 * @param {String} id - 
 * @param {String} pid - 
 * @param {String} children - 
 * @returns {Array} 
 * @summary 
 * @example
 * import { array2Tree } from 'yidash';
 * array2Tree([
 *      { id:1, pid:0, name:'a' ,age:16 },
 *      { id:2, pid:1, name:'b' ,age:18 },
 *      { id:3, pid:2, name:'c' ,age:22 },
 *      { id:4, pid:2, name:'d' ,age:25 },
 * ])
 * // 
 * [{
 *      id:1,
 *      pid:0,
 *      name:'a',
 *      age:16,
 *      children:[{
 *          id:2,
 *          pid:1,
 *          name:'b',
 *          age:18,
 *          children:[
 *              {
 *                  id:3,
 *                  pid:2,
 *                  name:'c',
 *                  age:22,
 *                  children:[]
 *              },
 *              {
 *                  id:4,
 *                  pid:2,
 *                  name:'d',
 *                  age:25,
 *                  children:[]
 *              }
 *          ]
 *      }]
 * }]
 */
export async function array2Tree(arrs, id = "id", pid = "pid", children = "children") {
    // idID
    let idObject = keyBy(arrs, id);

    // 
    let treeData = [];

    arrs.forEach((item) => {
        // 
        let pData = idObject[item[pid]];
        if (pData) {
            if (!pData[children]) {
                pData[children] = [];
            }
            pData[children].push(item);
        } else {
            treeData.push(item);
        }
    });
    return treeData;
}
